//global style
@font-face {
	font-family: Breathing;
	src: url(../webfonts/Breathing.ttf);
}
@font-face {
	font-family: SenticText;
	src: url(../webfonts/SenticText.otf);
}
@font-face {
	font-family: SenticText;
	src: url(../webfonts/SenticTextLight.otf);
}
@font-face {
	font-family: SenticText;
	src: url(../webfonts/SenticTextBold.otf);
}
@font-face {
	font-family: SenticText;
	src: url(../webfonts/SenticTextMedium.otf);
}

.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: SenticText, sans-serif;

	.main-wrapper {
		background-color: #e2e2e2;
		overflow-x: hidden;
		// padding-top: 70px;
		position: relative;
		width: 100%;
	}
}

//global style


//main section
section.main {
	background-color: #e2e2e2;
	text-align: center;

	.main-logo {
		padding: 25px 0;
	}

	.main-title {
		text-align: center;
		background-color: #fff;
		padding: 15px;
		color: $main_color;

		h1 {
			font-size: 2.5rem;
		}

		.pre-title {
			font-family: Breathing;
			font-size: 2rem;
		}
	}

	.main-image {
		margin-bottom: 25px;

		img {
			max-height: 570px;
			object-fit: cover;
			width: 100%;
		}
	}

	.main-text {
		margin-bottom: 30px;
		p {
			font-size: 1.2rem;
			line-height: 1.2;
		}
	}

	.second-title {
		margin: 25px 0;

		h2 {
			border-top: 2px solid $main_color;
			border-bottom: 2px solid $main_color;
			line-height: 2;
			color: $main_color;
			text-transform: uppercase;
		}
	}

	.content {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		gap: 15px;

		.company-box {
			text-align: center;

			.image {
				margin-bottom: 20px;

				img {
					max-height: 570px;
					object-fit: cover;
					width: 100%;
				}
			}

			.title {
				background-color: #fff;

				h3 {
					font-size: 1.5rem;
					font-weight: 700;
					color: $main_color;
					transition: all ease 0.3s;
					text-transform: uppercase;
					line-height: 2;
					margin-bottom: 10px;

					&:hover {
						color: $main_color;
					}
				}
			}
			.text {
				p {
					font-size: 1.2rem;
					line-height: 1.2;
				}
			}
		}
	}
	.map {
		margin: 20px 0 30px;
	}
}

//main section

//footer
footer {
	padding: 50px 0;
	background-color: $second_color;
	text-align: center;

	.content {
		.footer-logo {
			margin-bottom: 30px;
		}
		.social-media {
			h4 {
				font-size: 1.5rem;
				text-transform: uppercase;
				font-weight: 600;
				color: #fff;
				letter-spacing: 2px;
				margin-bottom: 15px;
			}
			a {
				margin: 0 3px;
				img {
					width: 40px;
				}
			}
		}
	}
}
//footer